.container{
    max-width: 1300px;
    width: 100%;
    margin: auto;
    /* background-color: black; */
    padding: 1rem;
}

section{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.card{
    flex:0 0 calc(33.33% - 20px);
    max-width:calc(33.33% - 20px) ;
    width: 100%;
    background-color: rgb(190, 222, 240);
    border-radius: 10px;
    padding: 1rem 0;
    margin-bottom: 20px;
    transition: .3s;
    border: 2px solid transparent;
}

.images{
    width: 24rem;
    border-radius:2rem ;
}

.name{
    margin-right: 15rem;
}