/* Pagination container */
.pagination {
    display: flex;
    list-style: none;
    padding: 0;
}

/* Pagination item */
.page-item {
    margin: 0;
}

/* Pagination links */
.page-link {
    text-decoration: none;
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin: 2px;
    cursor: pointer;
    color: #333;
}

/* Active page */
.page-item.active .page-link {
    background-color: #007bff;
    color: #fff;
    border-color: #007bff;
}

/* Disabled buttons */
.page-item.disabled .page-link {
    pointer-events: none;
    background-color: #f1f1f1;
    color: #888;
    border-color: #ccc;
}

/* Previous and Next buttons */
.prev-button,
.next-button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 5px 10px;
    border-radius: 4px;
    margin: 2px;
    cursor: pointer;
}

.prev-button.disabled,
.next-button.disabled {
    background-color: #f1f1f1;
    color: #888;
    pointer-events: none;
}

/* Active Previous and Next buttons */
.prev-button.active,
.next-button.active {
    background-color: #0056b3;
}

/* Pagination container */
.pagination {
    display: flex;
    list-style: none;
    padding: 0;
}


